.lb-loader, .lightbox {
	text-align:center;
	line-height:0;
}

body.lb-disable-scrolling {
	overflow:hidden;
}

.lightboxOverlay {
	position:absolute;
	top:0;
	left:0;
	z-index:9999;
	background-color:#000;
	opacity:.8;
	display:none;
}

.lightbox {
	position:absolute;
	left:0;
	width:100%;
	z-index:10000;
	font-weight:400;
	outline:0;
}

.lightbox .lb-image {
	display:block;
	height:auto;
	max-width:inherit;
	max-height:none;
	border-radius:3px;
	border:4px solid #fff;
}

.lightbox a img {
	border:none;
}

.lb-outerContainer {
	position:relative;
	width:250px;
	height:250px;
	margin:0 auto;
	border-radius:4px;
	background-color:#fff;
}

.lb-loader,.lb-nav {
	position:absolute;
	left:0;
}

.lb-outerContainer:after {
	content:"";
	display:table;
	clear:both;
}

.lb-loader {
	top:43%;
	height:25%;
	width:100%
}

.lb-cancel {
	display:block;
	width:32px;
	height:32px;
	margin:0 auto;
	background:url(/src/images/boat/lightBox/loading.gif) no-repeat;
}

.lb-nav {
	top:0;
	height:100%;
	width:100%;
	z-index:10;
}

.lb-container>.nav {
	left:0;
}

.lb-nav a {
	outline:0;
	background-image:url(data:image/gif;base64,R0lGODlhAQABAPAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==);
}

.lb-next,.lb-prev {
	height:100%;
	cursor:pointer;
	display:block;
}

.lb-nav a.lb-prev {
	width:34%;
	left:0;
	float:left;
	background:url(/src/images/boat/lightBox/prev.png) left 48% no-repeat;
	opacity:0;
	-webkit-transition:opacity .6s;
	-moz-transition:opacity .6s;
	-o-transition:opacity .6s;
	transition:opacity .6s;
}

.lb-nav a.lb-prev:hover {
	opacity:1;
}

.lb-nav a.lb-next {
	width:64%;
	right:0;
	float:right;
	background:url(/src/images/boat/lightBox/next.png) right 48% no-repeat;
	opacity:0;
	-webkit-transition:opacity .6s;
	-moz-transition:opacity .6s;
	-o-transition:opacity .6s;
	transition:opacity .6s;
}

.lb-nav a.lb-next:hover {
	opacity:1;
}

.lb-dataContainer {
	margin:0 auto;
	padding-top:5px;
	width:100%;
	border-bottom-left-radius:4px;
	border-bottom-right-radius:4px;
}

.lb-dataContainer:after {
	content:"";
	display:table;
	clear:both;
}

.lb-data {
	padding:0 4px;
	color:#ccc;
}

.lb-data .lb-details {
	width:85%;
	float:left;
	text-align:left;
	line-height:1.1em;
}

.lb-data .lb-caption {
	font-size:13px;
	font-weight:700;
	line-height:1em;
	}
.lb-data .lb-caption a {
	color:#4ae;
}

.lb-data .lb-number {
	display:block;
	clear:left;
	padding-bottom:1em;
	font-size:12px;
	color:#999;
}

.lb-data .lb-close {
	display:block;
	float:right;
	width:30px;
	height:30px;
	background:url(/src/images/boat/lightBox/close.png) top right no-repeat;
	text-align:right;
	outline:0;
	opacity:.7;
	-webkit-transition:opacity .2s;
	-moz-transition:opacity .2s;
	-o-transition:opacity .2s;
	transition:opacity .2s;
}

.lb-data .lb-close:hover {
	cursor:pointer;
	opacity:1;
}